<template>
    <div class="search-result-wrapper">
        <v-card
            class="mx-auto search-container"
            width='100%'
            color='rgba(0, 0, 0, .4)'
            rounded='0'
        >
            <Search
                class='search' 
                @searchFor='searchFor'
            />
        </v-card>
    <v-container>
        <v-card
            class="mx-auto"
            width="100%"
            max-width="700"
            v-for='(dataItem, index) in medData'
            :key='index'
        >

            <v-card-title>
            {{dataItem.title}}
            </v-card-title>

            <v-card-subtitle
                v-if="dataItem.content.length > 200"
            >
            {{dataItem.content.substring(0,200)+" ..."}}
            </v-card-subtitle>
            <v-card-subtitle
                v-else
            >
            {{dataItem.content}}
            </v-card-subtitle>

            <v-card-actions>
            <router-link
                :to='/post/+dataItem.id'
            >
            <v-btn
                color="orange lighten-2"
                text
            >
                Подробнее
            </v-btn>
            </router-link>
            <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-container>
    </div>
</template>

<script>

import Search from '../components/Search.vue'
import rowData from '../assets/Data.json'

export default {
    components: {
        Search
    },

    data: () => ({
        medData: []
    }),

    mounted() {
        this.searchFor(this.$router.param)
    },

    methods: {
        searchFor(value) {
            if (!value) return;
            this.medData = []
            const clinics = rowData.clinics.filter(item => (
                item.title.toLowerCase().indexOf(value.toLowerCase()) > -1
            ))
            const sickness = rowData.sickness.filter(item => (
                item.title.toLowerCase().indexOf(value.toLowerCase()) > -1
            ))
            if ((sickness || clinics)) {
                if (sickness) this.medData.push(...sickness)
                if (clinics) this.medData.push(...clinics)
            }

        }
    }

}
</script>

<style scoped>
    .search {
        margin: 0;
    }
    .search-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100px;
        padding: 1rem;
        margin-top: 1rem;
    }
    .search-result-wrapper{
        margin-top: 2rem;
    }
    .search-result-wrapper .container .v-card {
        margin-top: 2rem;
    }
</style>